import React from 'react'
import Profile from './Profile/Profile'
import Contact from './Contact/Contact'
import Projects from './Projects/Projects'
import './App.css'
import { useState } from 'react'
import ItemsCarousel from 'react-items-carousel';


export default function App() {
    const [sidebar, setSidebar] = useState({sidebarslide: '', pageslide: ''});
    const [pageState, setPageState] = useState({projects: '', profile: 'active', contact: ''});
    const [pageIsUnmounted, setToUnmount] = useState({projects: '', profile: '', contact: ''});
    const [activeItemIndex, setActiveItemIndex] = useState(0);
    const chevronWidth = 40;
    
    function handleIndex(index) {
      let newState = {projects: '', profile: '', contact: ''};
      setTimeout(() => {
        console.log(pageIsUnmounted)
        newState[index] = 'active';
        setPageState(newState);
      }, 500);  
    }


    function handleUnmount() {
     
        let component = Object.keys(pageState).find((key) => pageState[key] === 'active');
        let newState1 = {projects: '', profile: '', contact: ''};
        newState1[component] = 'unmount';
        setToUnmount(newState1);
        console.log(newState1);
      
    }

    function Header() {
     return( 
     <nav className ="navbar navbar-expand-xl ms-5">
      <a onClick={() => { handleUnmount();   handleIndex('profile');}} className="navbar-text text-light pe-5 nav-link">About Me</a>
      <a onClick={() => { handleUnmount();  handleIndex('contact')}} className="navbar-text text-light pe-5 nav-link">Contact me</a>
      <a onClick={() => { handleUnmount();  handleIndex('projects')}} className="navbar-text text-light pe-5 nav-link">Projects</a>    
    </nav>
     )
    }

   

     


    return(
      <div className="main-container">
        <div className="background-img"></div>
        <div className="home-container">
      <ItemsCarousel
        requestToChangeActive={setActiveItemIndex}
        activeItemIndex={activeItemIndex}
        numberOfCards={1}
        gutter={20}
        leftChevron={  <div class="arrow left"></div>}
        rightChevron={  <div class="arrow right"></div>}
        chevronWidth={chevronWidth}
      >
        <Profile />
        <Projects />
        <Contact />
      </ItemsCarousel>
        </div> 
        </div>
    )
}


